	module.exports = {"data":{"markdownRemark":{"frontmatter":{"title":"PEREPEL","type":"lyrics"},"html":"<h1>PEREPEL</h1>\n<section>\n<p>pereehal‐perevypolnil<br>\nperegryz‐perelomil</p>\n<p>pereros i perekrikival<br>\nperedelal‐perelil</p>\n<p>perelil‐lil‐lil‐lil‐lil‐lil‐lil</p>\n<p>Peredumal!<br>\nPerestal!</p>\n<p>Ja tebja pokazyval trjom solncam — dva potuhli,<br>\nkrajnee ostalos' ne prichjom.<br>\nJa nesu v karmanah tol'ko ruki<br>\ni ostanus', vidno, novichkom.</p>\n<p>perevypolnil‐pereehal<br>\npereosmyslil‐peresmotrel<br>\nperebezhal‐perezhal‐pererezal<br>\nperepel tvoj perepel<br>\nOj, perepel!</p>\n<p>perepel pel‐pel‐pel‐pere‐pel<br>\nda perebral</p>\n<p>perepel pel‐pel‐pel‐pere‐pel</p>\n<p>Peredelal!<br>\nPerestal!</p>\n<p>Pe‐re‐pel tvoj pe‐re‐pel!</p>\n<p>Ja tebja pokazyval trjom solncam — dva potuhli,<br>\nkrajnee ostalos' ne prichjom.<br>\nJa nesu v karmanah tol'ko ruki<br>\ni ostanus', vidno, novichkom.</p>\n<p>Ja tebja pokazyval trjom solncam — dva potuhli,<br>\nkrajnee ostalos' ne prichjom.<br>\nJa nesu v karmanah tol'ko sprjatannye ruki<br>\ni ostanus', vidno, novichkom.</p>\n<p>Ja zhivu sutulo, nezametno greet ruki<br>\ntjoplaja karmannaja voda.<br>\nV nebe po uglam iskal dorogu sytyj putnik,<br>\nno v sahare zheleznaja zvezda.</p>\n<p>Perepel tvoj perepel!<br>\nOh, perepel!</p>\n<h2>***</h2>\n<p>Ja tebja pokazyval trjom solncam — dva potuhli,<br>\nkrajnee ostalos' ne prichjom.<br>\nJa nesu v karmanah tol'ko ruki<br>\ni ostanus', vidno, novichkom.</p>\n<p>Sytym novichkom! Mne krepko ruki vjazhet<br>\ntjoplaja karmannaja voda!<br>\nSaharnye zvjozdy tajut, kak na pljazhe<br>\nlipkaja meduza, kak zvezda.</p>\n<p>Ja tebja pokazyval trjom solncam — dva potuhli,<br>\nkrajnee smejalos', zagadav<br>\nto, o chjom zhuzhzhat pustye ul'i:<br>\nmy verjovki ili provoda?</p>\n<p>Peredumal i ne ugadal.</p>\n</section>"},"site":{"siteMetadata":{"languages":{"defaultLangKey":"en","langs":["en","ru"]}}}},"pathContext":{"slug":"/en/lyrics/perepel/"}}

